* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.Badges {
  width: 1280px;
  height: 720px;
  display: -webkit-flex;
  display: flex;
}

.Team {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.Team img {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
}

.Chevron {
  width: 0px;
  height: 0px;
  border-top: 720px solid red;
  border-right: 100px solid transparent;
}

